const url = process.env.VUE_APP_USERS_URL

export default {
  async createAppointment (clientId, date, time, status) {
    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ date, time, status })
    }
    return fetch(`${url}/users/${clientId}/appointments`, options)
      .then(responseAsJson)
  },
  async getAppointments (clientId) {
    return fetch(`${url}/users/${clientId}/appointments`)
      .then(responseAsJson)
  },
  async updateAppointment (appointment) {
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Key': '12345'
      },
      body: JSON.stringify(appointment)
    }
    return fetch(`${url}/users/appointments`, options)
      .then(responseAsJson)
  },
  async deleteAppointment (appointment) {
    const options = {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(appointment)
    }
    return fetch(`${url}/users/appointments`, options)
      .then(responseAsJson)
  }
}

function responseAsJson (response) {
  if (response.status >= 400) {
    throw new Error('Error')
  }
  return response.json()
}
