const url = process.env.VUE_APP_REALTORS_URL

export default {
  async getClients (session) {
    const options = {
      headers: {
        'token': btoa(session.id)
      }
    }
    return fetch(`${url}/realtors/clients`, options)
      .then(response => {
        if (response.status >= 400) {
          throw new Error('Error')
        }
        return response.json()
      })
  },
  async createClient (clientName, clientEmail, clientPassword, clientIsRealtor) {
    const body = { email: clientEmail, password: btoa(clientPassword), name: clientName, role: clientIsRealtor ? 'realtor' : 'client' }
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      },
      body: JSON.stringify(body)
    }
    return fetch(`${url}/users`, options)
      .then(response => {
        if (response.status >= 400) {
          throw new Error('Unable to create user')
        }
        return response.json()
      })
  }
}
