<template>
  <div>
    <title-label title="REQUEST SHOWING"></title-label>
    <form class="flex-one flex-column" @submit.prevent="createAppointment">
      <label for="startDate" class="tertiary font-title font-2 m1-bottom">DATE</label>
      <input type="date" class="input m2-bottom" name="startDate" v-model="date">

      <label for="startTime" class="tertiary font-title font-2 m1-bottom">TIME</label>
      <input type="time" class="input m2-bottom" name="startTime" v-model="time">

      <div class="flex-one"></div>
      <button class="bg-clear contrast rounded3 p2 h-fill border-primary" :disabled="isLoading">REQUEST<i v-if="isLoading" class="fas fa-spinner fa-spin m1-left"></i></button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'CreateAppointment',
  data () {
    return {
      date: undefined,
      time: undefined,
      isLoading: false,
      error: undefined
    }
  },
  props: ['clientId', 'done'],
  inject: ['userClient'],
  methods: {
    createAppointment () {
      this.isLoading = true
      const self = this
      this.userClient.createAppointment(this.clientId, this.date, this.time, 'pending')
        .then(r => {
          self.isLoading = false
          self.done()
        })
        .catch(e => {
          self.isLoading = false
          self.error = 'Error creating appointment'
        })
    }
  }
}
</script>
