<template>
  <div>
    <div class="p3">
      <a class="tertiary font-title" @click="signOut"><i class="fas fa-sign-out-alt fa-rotate-180 m1-right"></i>EXIT</a>
    </div>

    <div class="p3 p1-top">
      <div class="m4-bottom">
        <div class="flex-row button alt" @click="$router.push('/disclosures')">
          <i class="fas fa-exclamation-circle m1-right"></i>
          <div>FILL OUT DISCLOSURES</div>
          <div class="text-right flex-one p1-right"><i class="fas fa-chevron-right"></i></div>
        </div>
      </div>

      <div class="appointments">
        <div v-for="(appointment, i) in appointments" :key="i" class="appointment-wrapper" :class="[appointment.status]">
          <title-label :title="appointment.status.toUpperCase()" class="m4-top"></title-label>
          <appointment :appointment="appointment" class="m2-bottom" @change="apptChanged"></appointment>
        </div>
        <p v-if="appointments.length === 0" class="tertiary">No appointments</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import TitleLabel from '@/components/TitleLabel'
import Appointment from '@/views/Appointment'

export default {
  name: 'Client',
  components: { TitleLabel, Appointment },
  data () {
    return {
      appointments: []
    }
  },
  props: ['clientId'],
  inject: ['userClient'],
  computed: {
    ...mapGetters(['session'])
  },
  methods: {
    ...mapActions(['setSession']),
    signOut () {
      this.setSession(undefined)
      this.$router.push('/')
    },
    fetchAppointments () {
      const self = this
      this.userClient.getAppointments(this.clientId)
        .then(appointments => {
          window.appts = appointments
          self.appointments = appointments
        })
    },
    apptChanged (updatedAppointment) {
    }
  },
  mounted () {
    this.fetchAppointments()
  }
}
</script>

<style lang="scss" scoped>
.appointments {
  .pending ~ .pending, .scheduled ~ .scheduled, .declined ~ .declined, .completed ~ .completed, .canceled ~ .canceled {
    .title-label {
      display: none;
    }
  }
}
</style>
