<template>
  <div>
    <div class="p3 flex-row">
      <a class="tertiary font-title" @click="$router.back()"><i class="fas fa-chevron-left m1-right"></i>BACK</a>
      <div class="flex-one"></div>
      <div class="p1-bottom p1-left border-bottom-secondary">{{client.name}}</div>
    </div>

    <div class="p3">
      <button class="button alt h-fill m1-bottom" @click="createAppointmentRequest"><i class="fas fa-plus-circle m1-right"></i>ADD SHOWING</button>

      <div class="appointments">
        <div v-for="(appointment, i) in appointments" :key="i" class="appointment-wrapper" :class="[appointment.status]">
          <title-label :title="appointment.status.toUpperCase()" class="m3-top"></title-label>
          <appointment :appointment="appointment" mode="realtor" class="m2-bottom" @change="apptChanged"></appointment>
        </div>
        <p v-if="appointments.length === 0" class="tertiary">No appointments</p>
      </div>
    </div>

    <transition name="slide-up">
      <div v-if="prompt" class="prompt fixed bg-base z2 shadow p3 flex-column">
        <div class="m4-bottom text-right">
          <a class="tertiary font-title" @click="prompt = false"><i class="fas fa-times m1-right"></i>CANCEL</a>
        </div>

        <create-appointment :client-id="clientId" :done="dismissPrompt"></create-appointment>
      </div>
    </transition>
  </div>
</template>

<script>
import TitleLabel from '@/components/TitleLabel'
import Appointment from '@/views/Appointment'
import CreateAppointment from '@/views/forms/CreateAppointment'
import { mapGetters } from 'vuex'

export default {
  name: 'RealtorClient',
  data () {
    return {
      prompt: false,
      appointments: []
    }
  },
  props: ['clientId'],
  computed: {
    ...mapGetters(['clients']),
    client () {
      return this.clients.find(c => c.id === parseInt(this.clientId))
    }
  },
  components: { TitleLabel, Appointment, CreateAppointment },
  methods: {
    fetchAppointments () {
      const self = this
      this.userClient.getAppointments(this.clientId)
        .then(appointments => {
          window.appts = appointments
          self.appointments = appointments
        })
    },
    createAppointmentRequest () {
      this.prompt = true
    },
    dismissPrompt () {
      this.fetchAppointments()
      this.prompt = false
    },
    apptChanged (updatedAppointment) {
    }
  },
  inject: ['userClient'],
  mounted () {
    this.fetchAppointments()
  }
}
</script>

<style lang="scss" scoped>
.appointments {
  .pending ~ .pending, .scheduled ~ .scheduled, .declined ~ .declined, .completed ~ .completed, .canceled ~ .canceled {
    .title-label {
      display: none;
    }
  }
}
</style>
