<template>
  <div>
    <div class="p3 flex-row">
      <a class="tertiary font-title" @click="signOut"><i class="fas fa-sign-out-alt fa-rotate-180 m1-right"></i>EXIT</a>
      <div class="flex-one"></div>
      <button class="bg-clear contrast rounded3 p1 border-primary" @click="addClient"><i class="fas fa-plus m1-right"></i>CLIENT</button>
    </div>

    <div class="p3 p1-top">
      <title-label title="CLIENTS"></title-label>
      <div v-for="(client, i) in clients" :key="i" class="p2-top p2-bottom border-bottom-tertiary" @click="viewClient(client)">
        {{ client.name }}
      </div>
      <p v-if="hasNoClients" class="tertiary">No active clients</p>
    </div>

    <transition name="slide-up">
      <div v-if="prompt" class="prompt fixed scrolly bg-base z2 shadow p3 flex-column">
        <div class="m4-bottom text-right">
          <a class="tertiary font-title" @click="prompt = false"><i class="fas fa-times m1-right"></i>CANCEL</a>
        </div>

        <title-label title="NEW CLIENT"></title-label>
        <form class="flex-one flex-column" @submit.prevent="createClient">
          <label for="name" class="tertiary font-title font-2 m1-bottom">CLIENT NAME</label>
          <input class="input m2-bottom" name="name" v-model="clientName">

          <label for="email" class="tertiary font-title font-2 m1-bottom">CLIENT EMAIL</label>
          <input class="input m2-bottom" name="email" v-model="clientEmail">

          <label for="password" class="tertiary font-title font-2 m1-bottom">CLIENT ENTRY PASSWORD</label>
          <input class="input m2-bottom" name="password" v-model="clientPassword">

          <div class="flex-row m4-top">
            <div class="flex-one"></div>
            <input type="checkbox" name="isAdmin" v-model="clientIsRealtor" class="m1-right">
            <label for="isAdmin" class="tertiary">Admin</label>
          </div>

          <div class="flex-one"></div>
          <button class="bg-clear contrast rounded3 p2 h-fill border-primary">CREATE</button>
        </form>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import TitleLabel from '@/components/TitleLabel'

export default {
  name: 'Realtor',
  components: { TitleLabel },
  data () {
    return {
      prompt: false,
      clientName: undefined,
      clientEmail: undefined,
      clientPassword: undefined,
      clientIsRealtor: false
    }
  },
  inject: ['realtorClient'],
  computed: {
    ...mapGetters(['session', 'clients']),
    hasNoClients () {
      return !this.clients || this.clients.length === 0
    }
  },
  methods: {
    ...mapActions(['setSession', 'setClients']),
    signOut () {
      this.setSession(undefined)
      this.$router.push('/')
    },
    addClient () {
      this.prompt = true
    },
    createClient () {
      const self = this
      this.realtorClient.createClient(this.clientName, this.clientEmail, this.clientPassword, this.clientIsRealtor)
        .then(res => {
          self.prompt = false
          self.refreshClients()
        })
    },
    viewClient (client) {
      this.$router.push(`/realtor/client/${client.id}`)
    },
    refreshClients () {
      const self = this
      this.realtorClient.getClients(this.session)
        .then(clients => {
          self.setClients(clients)
        })
    }
  },
  mounted () {
    this.refreshClients()
  }
}
</script>

