<template>
  <div class="appointment flex-column border-base2 rounded" :class="{'fade-in-out': isLoading}">
    <div class="p1 font-title text-center appt-title" :class="[appointment.status]">{{month}} {{day}}</div>

    <div class="flex-row align-center gradient-column">
      <div class="flex-one v-fill">
        <div v-if="showDecline" class="flex-column decline-button text-center" @click="updateStatus('declined')">
          <div class="m1-bottom"><i class="fas fa-times"></i></div>
          <p class="font-5">DECLINE</p>
        </div>

        <div v-if="showCancel" class="flex-column decline-button text-center" @click="updateStatus('canceled')">
          <div class="m1-bottom"><i class="fas fa-times"></i></div>
          <p class="font-5">CANCEL</p>
        </div>
      </div>

      <div class="flex-two flex-column align-center">
        <div class="flex-one font2 p1">{{displayTime}}</div>
        <div class="font-3 tertiary p1-bottom">{{dayOfWeek}}</div>
      </div>

      <div class="flex-one">
        <div v-if="showAccept" class="flex-column secondary text-center" @click="updateStatus('scheduled')">
          <div class="m1-bottom"><i class="fas fa-check"></i></div>
          <p class="font-5">ACCEPT</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Appointment',
  props: ['appointment', 'mode'],
  data () {
    return {
      isLoading: false
    }
  },
  computed: {
    date () {
      return new Date(this.appointment.date)
    },
    month () {
      return this.time.getShortMonth(this.appointment.date).toUpperCase()
    },
    day () {
      return this.time.getDayOfMonth(this.appointment.date)
    },
    dayOfWeek () {
      return this.time.getDayOfWeek(this.appointment.date).toUpperCase()
    },
    displayTime () {
      return this.time.formatTime(this.appointment.time)
    },
    showDecline () {
      return this.mode !== 'realtor' && this.appointment.status === 'pending'
    },
    showCancel () {
      if (this.mode === 'realtor') {
        return ['pending', 'scheduled'].includes(this.appointment.status)
      } else {
        return this.appointment.status === 'scheduled'
      }
    },
    showAccept () {
      return this.mode !== 'realtor' && this.appointment.status === 'pending'
    }
  },
  inject: ['time', 'userClient'],
  methods: {
    updateStatus (status) {
      if (this.appointment.status === 'pending') {
        this.deleteAppointment()
      } else {
        this.updateAppointment(status)
      }
    },
    deleteAppointment () {
      this.isLoading = true
      const self = this

      this.userClient.deleteAppointment(this.appointment)
        .then(r => {
          self.isLoading = false
          this.$emit('change', undefined)
        })
    },
    updateAppointment (status) {
      this.isLoading = true
      const self = this

      let updatedAppointment = { ...this.appointment }
      updatedAppointment.status = status
      this.userClient.updateAppointment(updatedAppointment)
        .then(r => {
          self.isLoading = false
          this.$emit('change', updatedAppointment)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.appointment {
  overflow: hidden;
  .decline-button {
    button { color: red; }
    color: red;
  }
  .appt-title.pending { border-bottom: solid 2px #24c1ff; }
  .appt-title.scheduled { border-bottom: solid 2px #0461c2; }
  .appt-title.completed { border-bottom: solid 2px #767569; }
  .appt-title.canceled { border-bottom: solid 2px #fc0000; }
  .appt-title.declined { border-bottom: solid 2px #ff9d10; }
}
</style>
